*{
  box-sizing: border-box;
}

body
{
  color: #222;
}
img{
  max-width: 100%;

}
main{
  padding:10 px;
  max-width: 700px;
  margin: 0 auto;
}
header{
  display: flex;
  justify-content: space-between;
  margin-bottom: 50px;
  margin-top: 20px;
  align-items: center;
}

header a{
  text-decoration: none;
  color: inherit;
}

header a.logo{
  font-weight: bold;
  font-size: 1.5rem;

}

header nav
{
  display: flex;
  gap: 15px;
}

div.post
{
    display: grid;
    grid-template-columns: 0.8fr 1.2fr;
    gap: 20px;
    margin-bottom: 30px;

}

div.post div.texts h2{
  margin: 0;
  font-size: 1.8rem;
}

div.post p.info
{
  margin: 6px 0;
  color: #999;
  font-size: 0.7rem;
  font-weight: bold;
  display: flex;
  gap: 10px;
}

div.post p.info a.author
{
  color: #333;
}

div.post p.summary
{
  margin: 10px 0;
  line-height: 1.2rem;
}

form.login , form.register{
  max-width: 400px;
  margin: 0 auto;
}

input {
  display: block;
  margin-bottom: 5px;
  width: 100%;
  padding: 5px 7px;
  border: 0;
  background-color: #eee;
  border-radius: 5px;
  border-bottom: 1 px solid #ccc;
}

button{
  width: 100%;
  display: block;
  background-color: #555;
  border: 0;
  border-radius: 5px;
  color: white;
  padding: 7px 0;
}

form h1{
  text-align: center;
}